import React from 'react';
import styled from 'styled-components'
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import { FaRegLightbulb } from 'react-icons/fa';
import { IoMdCheckmarkCircleOutline } from 'react-icons/io';
import { useStaticQuery, graphql } from 'gatsby';


const Testimonials = () => {

    const data = useStaticQuery(graphql`
        query {
            allFile(filter: {ext: {regex: "/(jpg)|(png)|(jpeg) /"}, name: {in: ["p1", "p2"]}}) {
              edges {
                node {
                  childImageSharp {
                    gatsbyImageData
                  }
                }
              }
            }
          }
    `)

    return (
        <TestimonialsContainer>
            <TopLine>
                Testimonials
            </TopLine>
            <Description>
                What People are Saying
            </Description>
            <ContentWrapper>
                <ColumnOne>
                    <Testimonial>
                        <IoMdCheckmarkCircleOutline css={`color: #3fffa8; font-size: 2rem; margin-bottom: 1rem;`} />
                        <h3>Soo Young</h3>
                        <p>"An experience of a lifetime. It feels like I've gained more experience than I would in my whole life if I didn't go on this trip. I can't describe how life-changing it's been for me!"</p>
                    </Testimonial>
                    <Testimonial>
                        <FaRegLightbulb css={`color: #f9b19b; font-size: 2rem; margin-bottom: 1rem;`} />
                        <h3>Sean Michaels</h3>
                        <p>"The greatest experience of my life! The people, the places, the sights - everything was just so amazing. I've come out of the experience with a much greater appreciation for the beauty of life and how wondrous our universe truly is. Simply magnificent.</p>
                    </Testimonial>
                </ColumnOne>
                <ColumnTwo>
                    {data.allFile.edges.map((image, key) => {
                        const img = getImage(image.node);
                        return <Images key={key} image={img} />
                    })}
                    <Images />
                </ColumnTwo>
            </ContentWrapper>
        </TestimonialsContainer>
    )
}

export default Testimonials;

const TestimonialsContainer = styled.div`
    width: 100%;
    background: #fcfcfc;
    color: #000;
    padding: 5rem calc((100vw - 1300px) /2);
    height: 100%;
`

const TopLine = styled.p`
    color: #077bf1;
    font-size: 1rem;
    padding-left: 2rem;
    margin-bottom: 0.75rem;
    font-weight: 700;
`

const Description = styled.p`
    text-align: start;
    padding-left: 2rem;
    margin-bottom: 4rem;
    font-size: clamp(1.5rem, 5vw, 2rem);
    font-weight: bold;
`

const ContentWrapper = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0 2rem;

    @media screen and (max-width: 768px) {
        grid-template-columns: 1fr;
    }
`

const ColumnOne = styled.div`
    display; grid;
    grid-template-rows: 1fr 1fr;
`

const Testimonial = styled.div`
    padding-top: 1rem;
    padding-right: 2rem;
    padding-bottom: 2rem;

    h3 {
        margin-bottom: 1rem;
        font-size: 1.5rem;
        font-style: italic;
    }

    p {
        color: #3b3b3b;
    }
`

const ColumnTwo = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 2rem;
    grid-gap: 10px;

    @media screen and (max-width: 500px) {
        grid-template-columns: 1fr;
    }
`

const Images = styled(GatsbyImage)`
    border-radius: 10px;
    height: 100%;
`